import { assestsUserAdmissionUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import style from "../../PlateformUser/PlateformUser.module.css";

const SelectDivisionCard = ({
  staff,
  selectedId,
  onSelect,
  selectList,
  isMultiSelect,
  onMultiSelect,
}) => {
  const { t } = useTranslation();
  const onSelectStaff = () => {
    onSelect(staff);
  };
  const onSelectMultipleStaff = () => {
    onMultiSelect(staff);
  };

  return (
    <>
      {isMultiSelect ? (
        <div
          className={style.assign_show_list_each}
          onClick={onSelectMultipleStaff}
          style={{
            border: "0.5px solid #cacaca",
            marginBottom: "0.7rem",
            padding: "0.7rem",
            borderRadius: "6px",
          }}
        >
          {selectList?.includes(staff?._id) ? (
            <img
              src={`${assestsUserAdmissionUrl}/check.svg`}
              alt="icon"
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
            />
          ) : (
            <img
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
              src={`${assestsUserAdmissionUrl}/uncheck.svg`}
              alt="icon"
            />
          )}

          <div className={style.assign_each_paragraph}>
            <h6>{staff?.classTitle}</h6>
          </div>
        </div>
      ) : (
        <div
          className={style.assign_show_list_each}
          style={
            selectedId === staff?._id
              ? {
                  backgroundColor: "rgba(75, 26, 133, 0.1)",
                  border: "0.5px solid #cacaca",
                  marginBottom: "0.7rem",
                  padding: "0.7rem",
                  borderRadius: "6px",
                }
              : {
                  border: "0.5px solid #cacaca",
                  marginBottom: "0.7rem",
                  padding: "0.7rem",
                  borderRadius: "6px",
                }
          }
          onClick={onSelectStaff}
        >
          <div className={style.assign_each_paragraph}>
            <h6>{staff?.classTitle}</h6>
          </div>
        </div>
      )}
      <BorderBottom
        customStyleBorder={{
          margin: "0",
        }}
      />
    </>
  );
};

export default SelectDivisionCard;
