import { useEffect, useState } from "react";
import style from "../../../StaffAdmisionAdmin/AdmissionScholarship/AdmissionScholarship.module.css";
import PopupWrapper from "../../../FinanceManager/FeesStructure/PopupWrapper";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import BorderBottom from "../../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { imageShowUrl } from "../../../../../../services/BaseUrl";
import { useAddUnapprovedStudentByClass } from "../../../../../../hooks/member_tab/class-api";
import QLoader from "../../../../../../Loader/QLoader";
import { useCatalogClassAllStudentList } from "../../../../../../optimized/Hooks/ApiHooks/ClassManager/class-manager-api";
import { nextPage } from "../../../../../../optimized/Utils/Functions/nextPage";
import QvipleLoader from "../../../../../../optimized/Loader/QvipleLoader";
import EmptyMessage from "../../../../../../optimized/Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../../../optimized/Loader/Loading";

const SelectUnapprovedStudent = ({ instituteId, onClose, onRefetch, cid }) => {
  const { t } = useTranslation();
  const [studentId, setStudentId] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [addUnapprovedStudentByClass] = useAddUnapprovedStudentByClass();

  const { catalogClassAllStudentList, catalogClassAllStudentListLoading } =
    useCatalogClassAllStudentList({
      data: {
        id: instituteId,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !instituteId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (catalogClassAllStudentList?.list?.length) {
      let obj = nextPage(catalogClassAllStudentList?.list?.length);
      setState(() => obj);
    }
  }, [catalogClassAllStudentList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !catalogClassAllStudentListLoading &&
      catalogClassAllStudentList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  const onSelectStudent = (sid) => {
    if (studentId?.includes(sid)) {
      setStudentId((prev) => prev?.filter((val) => val !== sid));
    } else {
      setStudentId((prev) => [...prev, sid]);
    }
  };

  const onAddStudent = () => {
    if (cid && studentId?.length) {
      setDisabled((pre) => !pre);
      addUnapprovedStudentByClass({
        cid: cid,
        studentAdd: {
          unapprove: studentId,
        },
      })
        .then(() => {
          onRefetch();
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <PopupWrapper onClose={onClose}>
      <div className={style.assign_role_container}>
        <section
          className={style.assign_role_title}
          style={{
            justifyContent: "space-between",
          }}
        >
          <h6>{t("select_students")}</h6>
          <div className={style.button_container}>
            <button
              className={style.next_btn}
              style={{
                right: "10%",
              }}
              onClick={onAddStudent}
            >
              {t("add")}
            </button>
            <img src="/images/close-post-icon.svg" onClick={onClose} alt="" />
          </div>
        </section>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
        <section
          style={{
            padding: "0 0.8rem",
          }}
        >
          <div
            className={style.search_container}
            style={{
              marginBottom: "0.6rem",
            }}
          >
            <div className={style.search_container_text}>
              <img
                className={style.searchIcon}
                alt="search icon"
                src="/images/search-dash-icon.svg"
              />
              <input
                type="text"
                placeholder={t("search-label")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
                autoFocus={true}
                style={{
                  border: "0.029vw solid rgba(0, 0, 0, 0.15)",
                  width: "450px",
                }}
              />
            </div>
          </div>
          {catalogClassAllStudentList?.list?.map((student, index) =>
            catalogClassAllStudentList?.list?.length === index + 1 ? (
              <section
                className={
                  studentId?.includes(student?._id)
                    ? `${style.student_container} ${style.select_hover_active}`
                    : `${style.student_container} ${style.select_hover}`
                }
                key={student?._id}
                ref={ref}
                onClick={() => onSelectStudent(student?._id)}
                style={{
                  padding: "0.6rem 0",
                  margin: "0",
                }}
              >
                <img
                  src={
                    student?.studentProfilePhoto
                      ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                      : "/images/member_tab/class/default_avatar.svg"
                  }
                  alt="student avatar"
                />
                <div className={style.student_container_name}>
                  <h6>{`${student?.studentFirstName} ${
                    student?.studentMiddleName ? student?.studentMiddleName : ""
                  } ${student?.studentLastName}`}</h6>
                  <p className={style.student_container_gr}>
                    {t("gr_number")} {student?.studentGRNO ?? ""}
                  </p>
                </div>
              </section>
            ) : (
              <section
                className={
                  studentId?.includes(student?._id)
                    ? `${style.student_container} ${style.select_hover_active}`
                    : `${style.student_container} ${style.select_hover}`
                }
                key={student?._id}
                onClick={() => onSelectStudent(student?._id)}
                style={{
                  padding: "0.6rem 0",
                  margin: "0",
                }}
              >
                <img
                  src={
                    student?.studentProfilePhoto
                      ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                      : "/images/member_tab/class/default_avatar.svg"
                  }
                  alt="student avatar"
                />
                <div className={style.student_container_name}>
                  <h6>{`${student?.studentFirstName} ${
                    student?.studentMiddleName ? student?.studentMiddleName : ""
                  } ${student?.studentLastName}`}</h6>
                  <p className={style.student_container_gr}>
                    {t("gr_number")} {student?.studentGRNO ?? ""}
                  </p>
                </div>
              </section>
            )
          )}

          {catalogClassAllStudentListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            catalogClassAllStudentList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No students found related this search."
              />
            )
          ) : (
            catalogClassAllStudentList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No students list."
              />
            )
          )}
          {disabled && <QLoader />}
        </section>
      </div>
    </PopupWrapper>
  );
};

export default SelectUnapprovedStudent;
