import React, { useEffect, useState } from "react";
import style from "../../../StaffAdmisionAdmin/AdmissionScholarship/AdmissionScholarship.module.css";
import PopupWrapper from "../../../FinanceManager/FeesStructure/PopupWrapper";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { imageShowUrl } from "../../../../../../services/BaseUrl";
import { useClassRemoveStudentToCatalog } from "../../../../../../hooks/member_tab/class-api";
import QLoader from "../../../../../../Loader/QLoader";

const RemoveSelectedStudent = ({ onClose, onRefetch, students, cid }) => {
  const { t } = useTranslation();
  const [studentId, setStudentId] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [allStudent, setAllStudent] = useState([]);
  const [classRemoveStudentToCatalog] = useClassRemoveStudentToCatalog();

  useEffect(() => {
    if (students?.length) {
      setAllStudent(students);
    }
  }, [students]);

  const onSearchStudent = (e) => {
    if (e.target.value) {
      const filterStudent = students?.filter((student) => {
        if (
          `${student?.studentFirstName} ${
            student?.studentMiddleName ? student?.studentMiddleName + " " : ""
          }${student?.studentLastName}`
            ?.toLowerCase()
            ?.includes(e.target.value?.toLowerCase())
        ) {
          return student;
        } else {
          return null;
        }
      });
      setAllStudent(filterStudent);
    } else {
      setAllStudent(students);
    }
  };

  const onSelectStudent = (sid) => {
    if (studentId?.includes(sid)) {
      setStudentId((prev) => prev?.filter((val) => val !== sid));
    } else {
      setStudentId((prev) => [...prev, sid]);
    }
  };

  const onAddStudent = () => {
    if (cid && studentId?.length) {
      setDisabled((pre) => !pre);
      classRemoveStudentToCatalog({
        cid: cid,
        studentList: {
          unapprove: studentId,
        },
      })
        .then(() => {
          onRefetch();
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <PopupWrapper onClose={onClose}>
      <div className={style.assign_role_container}>
        <section
          className={style.assign_role_title}
          style={{
            justifyContent: "space-between",
          }}
        >
          <h6>{t("select_remove_student")}</h6>
          <div className={style.button_container}>
            <button
              className={style.next_btn}
              style={{
                right: "10%",
              }}
              onClick={onAddStudent}
            >
              {t("remove")}
            </button>
            <img
              src="/images/close-post-icon.svg"
              onClick={onClose}
              alt="icon"
            />
          </div>
        </section>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
        <section
          style={{
            padding: "0 0.8rem",
          }}
        >
          <div
            className={style.search_container}
            style={{
              marginBottom: "0.6rem",
            }}
          >
            <div className={style.search_container_text}>
              <img
                className={style.searchIcon}
                alt="search icon"
                src="/images/search-dash-icon.svg"
              />
              <input
                type="text"
                placeholder={t("search-label")}
                onChange={onSearchStudent}
                autoFocus={true}
                style={{
                  border: "0.029vw solid rgba(0, 0, 0, 0.15)",
                  width: "450px",
                }}
              />
            </div>
          </div>

          {allStudent?.map((student) => (
            <section
              className={
                studentId?.includes(student?._id)
                  ? `${style.student_container} ${style.select_hover_active}`
                  : `${style.student_container} ${style.select_hover}`
              }
              key={student?._id}
              onClick={() => onSelectStudent(student?._id)}
              style={{
                padding: "0.6rem 0",
                margin: "0",
              }}
            >
              <img
                src={
                  student?.studentProfilePhoto
                    ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                    : "/images/member_tab/class/default_avatar.svg"
                }
                alt="student avatar"
              />
              <div className={style.student_container_name}>
                <h6>{`${student?.studentFirstName} ${
                  student?.studentMiddleName ? student?.studentMiddleName : ""
                } ${student?.studentLastName}`}</h6>
                <p className={style.student_container_gr}>
                  {t("gr_number")} {student?.studentGRNO ?? ""}
                </p>
              </div>
            </section>
          ))}
          {disabled && <QLoader />}
        </section>
      </div>
    </PopupWrapper>
  );
};

export default RemoveSelectedStudent;
