import { useEffect } from "react";
import { useState } from "react";
import { useClassAllStudent } from "../../../../../../hooks/member_tab/class-api";
import { imageShowUrl } from "../../../../../../services/BaseUrl";
import style from "./Catalog.module.css";
import { Link, useLocation, useParams } from "react-router-dom";
import SelectUnapprovedStudent from "./SelectUnapprovedStudent";
import { useTranslation } from "react-i18next";
import UniversalTab from "../../../DepartmentHead/Competetions/CompetitionTab/UniversalTab";
import BorderBottom from "../../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import RemoveSelectedStudent from "./RemoveSelectedStudent";
import ClassCatalogResuffleMenu from "../../../../../../optimized/UserStaffMember/ClsManager/ClassCatalog/ClassCatalogResuffleMenu";
import CreateButton from "../../../../../../optimized/Ui/Button/CreateButton";
import AssignStudentRollNumber from "../../../../../../optimized/UserStaffMember/ClsManager/ClassCatalog/AssignStudentRollNumber";
function Catalog({ cid, instituteId, carryParentState }) {
  const params = useParams();
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [addStudent, setAddStudent] = useState(false);
  const [students, setStudents] = useState([]);
  const [remove, setRemove] = useState(false);
  const [step, setStep] = useState("");
  const [resuffle, setResuffle] = useState(false);

  const tabList = [
    {
      name: t("catalog"),
      id: 0,
      url: `member/class/${params.cname}?a=catalog`,
    },
    {
      name: t("unapproved_student"),
      id: 1,
      url: `member/class/${params.cname}?a=catalog&unapproved`,
    },
  ];

  const { classAllStudent, classAllStudentRefetch } = useClassAllStudent({
    data: {
      cid: cid,
      page: 1,
      limit: 30,
    },
    skip: !cid,
  });

  useEffect(() => {
    if (cid) classAllStudentRefetch();
  }, [cid, classAllStudentRefetch]);

  useEffect(() => {
    if (classAllStudent?.classes?.ApproveStudent?.length) {
      setStudents(classAllStudent?.classes?.ApproveStudent);
    }
  }, [classAllStudent?.classes?.ApproveStudent]);

  const onSearchStudent = (e) => {
    if (e.target.value) {
      const filterStudent = classAllStudent?.classes?.ApproveStudent?.filter(
        (student) => {
          if (
            `${student?.studentFirstName} ${
              student?.studentMiddleName ? student?.studentMiddleName + " " : ""
            }${student?.studentLastName}`
              ?.toLowerCase()
              ?.includes(e.target.value?.toLowerCase())
          ) {
            return student;
          } else {
            return null;
          }
        }
      );
      setStudents(filterStudent);
    } else {
      setStudents(classAllStudent?.classes?.ApproveStudent);
    }
  };

  const onRefetch = () => {
    classAllStudentRefetch();
  };

  const onResuffleToggle = () => {
    setResuffle((pre) => !pre);
  };

  const onCloseStep = () => {
    setStep("");
  };
  return (
    <>
      <div className={style.catalog}>
        <UniversalTab
          tabList={tabList}
          activeIndexTab={getQuery.search?.substring(3) === "catalog" ? 0 : 1}
          showAs
          customStyleLink={{
            width: "50%",
          }}
          carryParentState={carryParentState}
        />
        <BorderBottom
          customStyle={{
            width: "100%",
            marginBottom: "0.5rem",
          }}
        />
        <div className={style.searchContainerr}>
          <div
            className={style.searchContainer}
            style={{
              width: "35%",
            }}
          >
            <img alt="" src="/images/search-dept-icon.svg" />
            <input
              type="text"
              placeholder={t("search-label")}
              onChange={onSearchStudent}
            />
          </div>
          <div className={style.class_catalog_funtcion}>
            <CreateButton
              label="assign_roll"
              customStyle={{
                margin: "0",
                padding: "0.3rem 0.5rem",
              }}
              onAction={() => setStep(1)}
            />
            <CreateButton
              label="add_students"
              customStyle={{
                margin: "0",
                padding: "0.3rem 0.5rem",
              }}
              onAction={() => setAddStudent(true)}
            />
            {/* <div
              className={style.sort_conatiner}
              onClick={() => setAddStudent(true)}
            >
              <span>{t("add_unapproved_students")}</span>
            </div> */}
            <CreateButton
              label="resuffle"
              customStyle={{
                margin: "0",
                padding: "0.3rem 0.5rem",
              }}
              onAction={onResuffleToggle}
            />

            <CreateButton
              label="remove"
              customStyle={{
                margin: "0",
                padding: "0.3rem 0.5rem",
              }}
              onAction={() => setRemove(true)}
            />
          </div>

          {/* <div className={style.checklistbtn}>Checklist</div> */}
        </div>

        <div className={style.itemsContainer}>
          {students?.map((stu, index) => (
            <Link
              to={`/q/${params.username}/student/profile`}
              state={{ sid: stu?._id, viewAs: "CLASS_CATALOG" }}
            >
              <div className={style.item}>
                <p>{stu?.studentROLLNO}</p>
                <img
                  alt=""
                  src={
                    stu?.studentProfilePhoto
                      ? `${imageShowUrl}/${stu?.studentProfilePhoto}`
                      : "/images/member_tab/class/default_avatar.svg"
                  }
                  loading="lazy"
                />
                <div className={style.student_catalog_name}>
                  {["64a79d08a59e4e19fe7ca1f3"]?.includes(instituteId) ? (
                    <h5>
                      {stu?.studentFirstName ?? ""}{" "}
                      {stu?.studentMiddleName ?? " "}
                      {stu?.studentLastName ?? ""}
                    </h5>
                  ) : (
                    <h5>
                      {stu?.studentLastName ?? ""} {stu?.studentFirstName ?? ""}{" "}
                      {stu?.studentMiddleName ?? " "}
                    </h5>
                  )}

                  <h6>
                    {t("gr_number")}{" "}
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {stu?.studentGRNO ?? ""}
                    </span>
                    {stu?.student_prn_enroll_number ? (
                      <span
                        style={{
                          fontWeight: "500",
                        }}
                      >
                        {`, ${t("enrollment_number_only")}: ${
                          stu?.student_prn_enroll_number
                        }`}
                      </span>
                    ) : null}
                  </h6>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>
      {addStudent && (
        <SelectUnapprovedStudent
          cid={cid}
          onClose={() => setAddStudent(false)}
          instituteId={instituteId}
          onRefetch={classAllStudentRefetch}
        />
      )}
      {remove && (
        <RemoveSelectedStudent
          onClose={() => setRemove(false)}
          students={students}
          onRefetch={onRefetch}
          cid={cid}
        />
      )}
      {resuffle ? (
        <ClassCatalogResuffleMenu
          onClose={onResuffleToggle}
          onRefetch={classAllStudentRefetch}
          cid={cid}
          students={students}
        />
      ) : null}

      {step === 1 ? (
        <AssignStudentRollNumber
          onClose={onCloseStep}
          students={students}
          cid={cid}
          onRefetch={classAllStudentRefetch}
        />
      ) : null}
    </>
  );
}

export default Catalog;
