import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import SelectDivisionCard from "./SelectDivisionCard";
import { useClassDivisionResultList } from "../../../../hooks/member_tab/class-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../PlateformUser/PlateformUser.module.css";
const SelectDivision = ({
  cid,
  title,
  onClose,
  selectedId,
  onSelectStaff,
  isMultiSelect,
  onMultiSelect,
  selectList,
  onDone,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { classDivisionResultList, classDivisionResultListLoading } =
    useClassDivisionResultList({
      data: {
        cid: cid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !cid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (classDivisionResultList?.list?.length) {
      let obj = nextPage(classDivisionResultList?.list?.length);
      setState(() => obj);
    }
  }, [classDivisionResultList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onSelect = (value) => {
    if (selectedId === value?._id) {
    } else {
      onSelectStaff(value);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t(title ?? "select_classes")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              {isMultiSelect && (
                <CreateButton
                  label={t("select")}
                  count={selectList?.length}
                  onAction={onDone}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              )}
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.3rem",
          }}
        >
          <div className={style.assign_search_container}>
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {classDivisionResultList?.list?.map((staff, index) =>
            classDivisionResultList?.list?.length === index + 1 ? (
              <div key={staff?._id} ref={ref}>
                <SelectDivisionCard
                  staff={staff}
                  selectedId={selectedId}
                  onSelect={onSelect}
                  selectList={selectList}
                  isMultiSelect={isMultiSelect}
                  onMultiSelect={onMultiSelect}
                />
              </div>
            ) : (
              <SelectDivisionCard
                staff={staff}
                key={staff?._id}
                selectedId={selectedId}
                onSelect={onSelect}
                selectList={selectList}
                isMultiSelect={isMultiSelect}
                onMultiSelect={onMultiSelect}
              />
            )
          )}
          {classDivisionResultListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            classDivisionResultList?.list?.length === 0 && (
              <EmptyMessage
                title="No class found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            classDivisionResultList?.list?.length === 0 && (
              <EmptyMessage
                title="No calss."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default SelectDivision;
